import React, { useState } from 'react'
import { useDispatch, useSelector } from "react-redux";
import { useHistory } from 'react-router-dom';
import { toast } from "react-toastify";

const Addcontact = () => {
	const [name, setName] = useState('')
	const [email, setEmail] = useState('')
	const [number, setNumber] = useState('')

	const contacts = useSelector((state) => state)
	const dispatch = useDispatch()
	const history = useHistory()
	// console.log(contacts);

	const handleSubmit = (e) => {
		e.preventDefault();

		const checkEmail = contacts.find(contact => contact.email === email && email);
		const checkNumber = contacts.find((contact) => contact.number === parseInt(number));

		if (!email || !number || !name) {
			return toast.warning("please fill in all fields")
		}

		if (checkEmail) {
			return toast.error("This Email Alraedy Exist")
		}
		if (checkNumber) {
			return toast.error("This Number Alraedy Exist")
		}

		const data = {
			id: contacts[contacts.length - 1].id + 1,
			name,
			email,
			number

		}
		dispatch({ type: "ADD_CONTACT", payload: data })
		toast.success("Student Added Succsesfully")
		history.push("/");
	}
	return (

		<div className="container">
			<div className="row">
				<h1 className="display-3 my-4   text-center">
					Add Student
				</h1>
				<div className="col-md-6 shadow mx-auto p-6">
					<form onSubmit={handleSubmit}>
						<div className="form-group">
							<input type="text"
								placeholder='name'
								className="form-control"
								value={name} onChange={e => setName(e.target.value)} />
						</div>

						<div className="form-group">
							<input type="email"
								placeholder='email'
								className="form-control"
								value={email} onChange={e => setEmail(e.target.value)} />
						</div>

						<div className="form-group">
							<input type="number"
								placeholder='phone Numer'
								className="form-control"
								value={number} onChange={e => setNumber(e.target.value)} />
						</div>

						<div className="form-group">
							<input type="submit" value="Add Student" className="btn btn-block btn-dark py-2 my-4 " />
						</div>
					</form>
				</div>
			</div >
		</div >


	)
}
export default Addcontact;