import React from 'react'
import { Link } from 'react-router-dom'

const Navbar = () => {
	return (
		<div >
			<div className="navbar navbar-expand-lg navbar-drak bg-dark py-2">
				<Link Link to="/" className="navbar-brand ml-5" >
					React Reducx Contact App
				</Link >
			</div >
		</div >
	)
}
export default Navbar;